"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { motion, AnimatePresence } from "framer-motion";
import { Moon, Sun, ChevronDown, Menu, X } from "lucide-react";

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    if (darkMode) document.documentElement.classList.add("dark");
    else document.documentElement.classList.remove("dark");
  }, [darkMode]);

  return (
    <motion.nav
      initial={{ y: -40, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.4 }}
      className={`
        fixed top-0 left-0 w-full z-50 
        backdrop-blur-2xl
        bg-gradient-to-r from-white/10 via-white/20 to-white/10
        dark:bg-gradient-to-r dark:from-black/10 dark:via-black/20 dark:to-black/10
        border-b border-white/30 dark:border-white/10
        shadow-[0_8px_32px_rgba(31,38,135,0.3)]
        transition-all duration-300
        ${scrolled ? "py-2" : "py-4"}
      `}
    >
      <div className="max-w-7xl mx-auto flex items-center justify-between px-6">

        {/* LOGO */}
        <div className="flex items-center gap-2">
          <Image
            src="/logo.png"
            width={42}
            height={42}
            alt="Logo"
            className="object-contain drop-shadow-xl"
          />
        </div>

        {/* LINKS DESKTOP */}
        <div className="hidden md:flex items-center gap-8">
          {navLink("/", "Home")}
          {navLink("/about", "About")}

          {/* DROPDOWN */}
          <div
            className="relative"
            onMouseEnter={() => setDropdownOpen(true)}
            onMouseLeave={() => setDropdownOpen(false)}
          >
            <button
              className="
                px-4 py-2 font-medium flex items-center gap-1 
                text-emerald-600 dark:text-gray-300
                hover:text-emerald-700 dark:hover:text-white
                relative group
              "
            >
              Services
              <ChevronDown size={18} className="mt-[2px]" />

              {/* underline */}
              <span
                className="
                  after:content-[''] after:absolute after:left-1/2 after:-bottom-1
                  after:w-0 after:h-[2px] after:bg-emerald-600 dark:after:bg-white
                  after:transition-all after:duration-300 after:-translate-x-1/2
                  group-hover:after:w-full
                "
              />
            </button>

            <AnimatePresence>
              {dropdownOpen && (
                <motion.div
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  transition={{ duration: 0.15 }}
                  className="absolute left-0 mt-2 py-3 px-4 
                    rounded-xl backdrop-blur-xl bg-white/20 dark:bg-black/20 
                    border border-white/30 shadow-xl"
                >
                  <div className="flex flex-col gap-2">
                    {dropdownItem("/cleaning", "Cleaning")}
                    {dropdownItem("/maintenance", "Maintenance")}
                    {dropdownItem("/windows", "Window Washing")}
                  </div>
                </motion.div>
              )}
            </AnimatePresence>
          </div>

          {/* CONTACT BUTTON */}
          <motion.div whileHover={{ scale: 1.07 }} whileTap={{ scale: 0.96 }}>
            <Link
              href="/contact"
              className="px-5 py-2 rounded-xl font-medium text-white relative overflow-hidden
              bg-emerald-600 shadow-lg"
            >
              <span className="relative z-10">Contact us</span>
              <motion.span
                initial={{ x: "-100%" }}
                whileHover={{ x: 0 }}
                transition={{ duration: 0.4 }}
                className="absolute inset-0 bg-emerald-700"
              />
            </Link>
          </motion.div>
        </div>

        {/* RIGHT SIDE BUTTONS */}
        <div className="flex items-center gap-4">
          {/* dark mode */}
          <button
            onClick={() => setDarkMode(!darkMode)}
            className="p-2 rounded-full bg-white/20 dark:bg-black/20
             border border-white/30 shadow-md backdrop-blur-xl"
          >
            <motion.div
              initial={false}
              animate={darkMode ? { rotate: 360 } : { rotate: 0 }}
              transition={{ duration: 0.5 }}
            >
              {darkMode ? <Sun size={20} /> : <Moon size={20} />}
            </motion.div>
          </button>

          {/* hamburger */}
          <button
            className="md:hidden p-2 rounded-lg bg-white/20 backdrop-blur-xl"
            onClick={() => setMenuOpen(!menuOpen)}
          >
            {menuOpen ? <X size={26} /> : <Menu size={26} />}
          </button>
        </div>
      </div>

      {/* MOBILE MENU */}
      <AnimatePresence>
        {menuOpen && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            className="md:hidden mt-4 px-6 pb-4 flex flex-col gap-4
            backdrop-blur-xl bg-white/10 dark:bg-black/10 border-t border-white/20 pt-3"
          >
            {navLinkMobile("/", "Home")}
            {navLinkMobile("/about", "About")}

            <details>
              <summary className="cursor-pointer text-emerald-700 dark:text-white">
                Services
              </summary>
              <div className="ml-4 mt-2 flex flex-col gap-2">
                {dropdownItem("/cleaning", "Cleaning")}
                {dropdownItem("/maintenance", "Maintenance")}
                {dropdownItem("/windows", "Window Washing")}
              </div>
            </details>

            <Link
              href="/contact"
              className="w-full text-center mt-2 px-5 py-2 rounded-xl
              bg-emerald-600 text-white shadow-lg"
            >
              Contact us
            </Link>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.nav>
  );
}

/* --------------------------------- HELPERS --------------------------------- */

function navLink(href: string, text: string) {
  return (
    <Link
      href={href}
      className="
        px-4 py-2 font-medium text-emerald-600 dark:text-gray-300
        hover:text-emerald-700 dark:hover:text-white
        relative group
      "
    >
      {text}

      {/* underline */}
      <span
        className="
          after:content-[''] after:absolute after:left-1/2 after:-bottom-1
          after:w-0 after:h-[2px] after:bg-emerald-600 dark:after:bg-white
          after:transition-all after:duration-300 after:-translate-x-1/2
          group-hover:after:w-full
        "
      />
    </Link>
  );
}

function navLinkMobile(href: string, text: string) {
  return (
    <Link
      href={href}
      className="
        text-lg text-emerald-700 dark:text-white
        hover:text-emerald-600 dark:hover:text-gray-200
      "
    >
      {text}
    </Link>
  );
}

function dropdownItem(href: string, text: string) {
  return (
    <Link
      href={href}
      className="
        px-2 py-1 rounded-md
        text-emerald-700 dark:text-gray-200
        hover:text-emerald-900 dark:hover:text-white
        transition-colors
      "
    >
      {text}
    </Link>
  );
}